.card__title {
  margin: 0;
  padding-bottom: 15px;
  font-size: var(--font-size-l);
  line-height: var(--line-height-l);
  font-weight: var(--font-weight-400);
}

@media screen and (max-width: 640px) {
  .card__title {
    max-width: 245px;
    font-size: 14px;
    line-height: 1.3;
    padding-bottom: 10px;
  }
}