.quiz__answers {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 20px;
  grid-area: quiz__answers;
  max-width: 560px;
}

@media screen and (max-width: 640px) {
  .quiz__answers {
    font-size: 14px;
    line-height: 1.3;
    max-width: 300px;
    gap: 13px;
  }
}